.login{height:80vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.login-logo{display:flex;align-items:end;width:328px;margin-bottom:10px}.login-logo-img{width:180px;height:180px}.login-logo-text{font-weight:500;font-size:54px;font-family:"서울한강";letter-spacing:1px}.login-logo-text-small{text-align:center;font-size:21px;font-weight:400;color:#929292}.login-input{position:relative}.login-input img{left:20px;top:43%;transform:translateY(-50%);position:absolute}.login-input input{padding-left:50px;background-color:#e9e9e9;width:278px;height:55px;border-radius:30px;border:none;margin-bottom:8px;font-size:16px;font-weight:400;font-family:"Spoqa Han Sans Neo"}.login-button{width:328px;height:55px;border-radius:30px;border:none;display:flex;justify-content:center;align-items:center;margin-bottom:8px;font-size:20px;font-weight:500;color:#fff;cursor:pointer}.login-button.b1{background-color:#929292}.login-button.b2{background-color:#494949}